.nav_bar{
  height: 100%;
  position: relative;
  /* max-height: 100%;1 */
}

nav {
  /* border: 3px solid black; */
  display: flex;
  justify-content: space-around;
  text-align: center;
  align-items: center;
  min-height: 15vh;
}

nav h1{
  font-size: 50px;
  font-family: 'Merriweather', serif;
}

.nav_item {
  width: 40%;
  display: flex;
  justify-content: space-around;
  align-items: center;
  list-style-type: none;
}

.nav_item li{
  font-size: 15px;
  text-decoration: none;
  font-family: 'Merriweather', serif;
}

#menu_icon {
  height: 40px;
  width: 40px;
}

/* Individual item */
.bm-item {
  display: inline-block;

  /* Our sidebar item styling */
  text-decoration: none;
  margin-bottom: 10px;
  color: #d1d1d1;
  transition: color 0.2s;
}

/* Change color on hover */
.bm-item:hover {
  color: white;
}

/* The rest copied directly from react-burger-menu docs */

/* Position and sizing of burger button */
.bm-burger-button {
  position: fixed;
  width: 36px;
  height: 30px;
  right: 10%;
  top: 50px;
}

/* Color/shape of burger icon bars */
.bm-burger-bars {
  background: #ffffff;
}

/* Position and sizing of clickable cross button */
.bm-cross-button {
  right: 15px !important;
}

/* Color/shape of close button cross */
.bm-cross {
  background: #bdc3c7;
  height: 30px !important;
  width: 3px !important;
}

/* General sidebar styles */
.bm-menu {
  background: rgba(0, 0, 0, 0.8);
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  padding: 2.5em 1.5em 0;
  font-size: 1.15em;
}

.bm-menu-wrap {
  position: fixed;
  top: 0;
  width: 100% !important;
}

/* Morph shape necessary with bubble or elastic */
.bm-morph-shape {
  fill: #373a47;
}

/* Wrapper for item list */
.bm-item-list {
  color: #b8b7ad;
  border: none!important;
  color: transparent!important;
}

/* Styling of overlay */
.bm-overlay {
  background: rgba(0, 0, 0, 0.8);
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  padding: 2.5em 1.5em 0;
  font-size: 1.15em;
}

#page-wrap {
  display: flex!important;
  flex-direction: column;
  width: 100%;
  height: 100%;
  align-items: center;
  justify-content: center;
  line-height: 75px;
  outline: none!important;
}

#page-wrap a{
  font-family: 'Merriweather', serif;
  font-size: 20px;
}

.swiper-container {
  /* border: 3px solid black; */
  width: 900px;
  height: 500px;
  margin: 0px auto;
  margin-top: 50px;
  overflow: hidden;
  object-fit: cover;
  object-position: center;
  box-shadow: 10px 10px 5px black;
}

.swiper-wrapper {
  width: 400%;
}

.swiper-slide {
  /* border: 3px solid pink; */
  max-width: 100% !important;
  height: auto !important;
  overflow: hidden;
  margin: 0px !important;
  object-fit: cover;
  object-position: center;
}

.swiper-slide img {
  width: 900px;
  height: 100%;
}

.title_cover {
  position: relative;
  background: rgba(0,0,0,0.85);
  height: 50px;
  width: 100%;
  bottom: 50px;
}

.title_cover h1 {
  position: absolute;
  font-size: 18px;
  bottom: 15px;
  left: 10px;
  font-family: 'Merriweather', serif;
}

.swiper-button-prev,
.swiper-button-next {
  color: #Cf3416 !important;
}

.swiper-pagination-bullet.swiper-pagination-bullet-active {
  background: #Cf3416 !important;
}

.product_page{
  /* border: 3px solid black; */
  margin: 0px auto;
  width: 85%;
  height: 80%;
  overflow-y: scroll;
  text-align: center;
  display: flex;
  flex-wrap: wrap;
  background: rgba(0,0,0,0.92);
}

.product_page::-webkit-scrollbar,
.aboutus_page::-webkit-scrollbar{
  display: none;
}

.display_box {
  display: flex;
  justify-content: left;
  /* border: 3px solid white; */
  width: 22%;
  height: 25%;
  margin: 10px auto;
  overflow: hidden;
  position: relative;
}

.display_box img {
  width: 100%;
  height: auto;
}

.img_title {
  position: relative;
  padding: 5px 0px;
  font-size: 14px;
  font-family: 'Merriweather', serif;
}

.location_title_cover {
  position: absolute;
  background: rgba(0,0,0,0.5);
  height: auto;
  width: 100%;
  bottom: 0px;
}

.aboutus_page{
  /* border: 3px solid black; */
  margin: 0px auto;
  padding: 20px;
  width: 40%;
  height: 70%;
  overflow-y: scroll;
  text-align: center;
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  justify-content: center;
  background: rgba(0,0,0,0.92);
}

.aboutus_page img {
  width: 30%;
  height: auto;
  border-radius: 50%;
  margin: 20px auto;
}

.aboutus_page p {
  width: 80%;
  margin: 0px auto;
  text-align: justify;
}

@media only screen and (max-width: 1000px) {
  nav h1 {
    position: fixed;
    left: 10%;
    top: 30px;
    width: 36px;
    height: 30px;
  }
  .swiper-container {width: 100%;height: 550px;}
  .swiper-slide img {width: 100%;}
  .display_box {width: 40%;}
  .aboutus_page {
    width: 60%;
    height: 80%;
  }
}

@media only screen and (max-width: 500px) {
  #background_overlay::before {
    content: "";
    background-image: url(https://media.selectiveasia.com/500x800/1667-2667_-507_0_0/9400.jpg);
    position: fixed;
    background-attachment: scroll;
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    filter: blur(4px);
    margin: -10px; /* this will do the trick */
  }
  .nav_bar {
    color: black;
  }
  .swiper-container {margin-top: 0px;height: 75%;}
  .product_page {width: 95%; height: 75%;}
  .display_box {width: 80%;}
  #my_name {color: black;}
  .aboutus_page {
    width: 80%;
  }
  .aboutus_page p {
    width: 100%;
  }
}