* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  color: white;
}

#my_name {
  position: absolute;
  bottom: 10px;
  left: 10px;
  font-size: 10px;
  color: white;
}

#root {
  height: 100%;
}

html, body, #root {
  height: 100%;
}

#background_overlay::before {
  content: "";
  background-image: url(https://storage.googleapis.com/demofg0001/gallery_background_img.webp);
  position: fixed;
  background-attachment: scroll;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  filter: blur(2px);
  margin: -10px; /* this will do the trick */
}